import { readonly, ref } from 'vue';
import { pagesData as pagesDataRaw } from '@internal/pagesData';
export const pagesData = ref(readonly(pagesDataRaw));
export const usePagesData = () => {
    return pagesData;
};
if (import.meta.webpackHot) {
    import.meta.webpackHot.accept('@internal/pagesData', () => {
        pagesData.value = readonly(pagesDataRaw);
    });
}
if (import.meta.hot) {
    import.meta.hot.accept('@internal/pagesData', () => {
        pagesData.value = readonly(pagesDataRaw);
    });
}
